import { getPosts } from '@/api/posts'

export default {
  async asyncData() {
    const tag = 'ministry'
    const posts = await getPosts(tag)
    return { posts }
  },
  head() {
    return {
      title: 'Ministries',
      meta: [
        {
          hid: 'description',
          name: 'description',
          content: 'A list of MCEA ministries'
        }
      ]
    }
  }
}
